<template>
	<transition name="translate">

		<module :imgSrc="imgSrc" :loaded="loaded" :list="list" :isStart="isStart" @startPlay="startPlay">
			
			<div class="set-intro-detail" slot="title">
				<div>
					<h3 class="set-singer">{dissname}}</h3>
				<p class="set-fans"><img :scr="headurl"></img>{{nickname}}</p>
				<p class="set-singer-intro
				">播放量{{visitnum}}</p>
				</div>
				
			</div>
			<div slot="list" class="set-music-list">
				<h4>歌曲 共{{total}}首</h4>
				<ul>
					<li class="list-item" v-for="(item,index) in list" @click="playOne(item)">
						<span>{{index+1}}</span>
						<h5>{{item.songname}}</h5>
						<p>{{item.singer}}</p>
					</li>
					<li v-if="!isAll" class="list-item load-more" @click="downloadMore">点击加载更多数</li>
					<li v-else class="list-item loaded-all">已经全部加载</li>
				</ul>
			</div>


		</module>
		
		
	</transition>

</template>

<script>
import Module from "components/base/Module"
import {getDissList} from "common/js/music"
import {getDiss} from "api/song"
import {ERR_OK} from "api/config"
import {mapGetters,mapActions,mapMutations} from "vuex"
import {playMode} from "common/js/config"
export default{ 
	name:"set",
	components:{
		Module,
	},
	created(){
		this._getSet();
		if(!this.set){

			this.back();
			return;
		}
		

	},
	watch:{
		set:function(newv,old){
			if(newv!=old){
				this._getSet();
			}
		}
	},
	destoryed(){
	},
	methods:{
		startPlay(val){

this.isStart=val;
		},
		playOne(item){
			this.selectPlay({list:[item],index:0,fullScreen:false})
			this.setMode(playMode.loop);
			this.startPlay(true)

		},
		downloadMore(){
			if(this.on){
				this.on=false;
				
				this._getSet();
			}
			
		},
		
		_getSet(){
			console.log("准备拉数据")

			getDiss(this.set,this.p).then(res=>{
console.log(res)
				if(res.code===ERR_OK){
					
					let data=res.cilist[0];
					this.dissname=data.dissname;
					this.nickname=data.nickname;
					this.visitnum=Math.floor(data.visitnum/1000)/10+"万";
					this.total=data.songnum;
					this.imgSrc=`https://y.gtimg.cn/music/photo_new/T006R300x300M000${data.singer_mid}.jpg?max_age=2592000`;
					data.songlist.forEach(item=>{
						this.list.push(getDissList(item))
					})
					this.loaded=true;
					this.p++;
					this.on=true;

				}
			})
		},
		back(){
			this.$router.go(-1);
		},
		...mapMutations({
"setMode":"SET_MODE"
		}),
		...mapActions([
			"selectPlay"
			])
		
	},
	computed:{
		isAll(){
			return this.list.length>=this.total;
		},
		
		...mapGetters([
			"set"
			])
	},
	data(){
		return{
			loaded:false,
			p:0,
			dissname:"",
			nickname:"",
			visitnum:"",
			total:"",
			list:[],
			headurl:"",
			imgSrc:"",
			on:true,
			isStart:false,
		}
	}


}

</script>

<style lang="less" type="text/less" scoped>
@import "~common/less/variable";
@import "~common/less/mixin";

.set-intro-detail{

	flex:1;
	.list(column);
	height:250px;
	justify-content: center;
	.set-singer{
		font-size: @font-size-large-s;
		line-height: 2;
	}
	.set-fans,.set-singer-intro
	{
		font-size:@font-size-medium;
		line-height: 1.5;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}
}

.set-music-list{

	color:@color-text;
	h4{
		padding:0 28px;
		font-size: @font-size-medium;
		line-height: 108px;
		color:#777;
	}
	.list-item{
		padding-left: 90px;
		position: relative;
		height: 124px; 
		.list(column);
		justify-content: center;
		line-height: 1.4;
		.no-wrap;
		span{
			position: absolute;
			color:@color-danger;
			left: 0;
			top: 0;
			line-height: 108px;
			width: 90px;
			text-align:center;
		};
		h5{
			color:rgba(0,0,0,.8);
			font-size: @font-size-large;
			.no-wrap;
		}
		p{
			font-size: @font-size-medium;
			.no-wrap;
			color:rgba(0,0,0,.6)
		}
		&.load-more,&.loaded-all{
			padding-left:0;
			text-align: center;
			font-size: @font-size-medium-x;
			color:@color-text-d;

		}
	}
}






</style>


