export function getDiss(id,p){
	console.log(id,p)
	let url="https://c.y.qq.com/qzone/fcg-bin/fcg_ucc_getcdinfo_byids_cp.fcg",

	data=Object.assign({},commonParams,{
notice:0,
platform:"h5",
needNewCode:1,
new_format:1,
pic:500,
disstid:`${id}`,
type:1,
json:1,
utf8:1,
onlysong:0,
picmid:1,
nosign:1,
song_begin:`${p}`,
song_num:1524298192568,
_:1524445762323,
format:"json"
	});
	return jsonp(url,data,ops)
}

export function getTop(id){
	let url="https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg",
	data=Object.assign({},commonParams,{
notice:0,
platform:"h5",
needNewCode:1,
tpl:3,
page:"detail",
type:"top",
topid:`${id}`,
_:1524454795917
	});
	return jsonp(url,data,ops)

}


