<template>
	<div id="app" cloak>
		<div  id="app-contain-wrap">
			<m-head class="app-top"></m-head>
			
			<keep-alive>
				<router-view></router-view>
			</keep-alive>

			
		</div>


		<player v-if="fullScreen"></player>
		<audio autoplay :src="audioSrc"></audio>




		<!-- <player></player> -->

		
	</div>
</template>
<script>
import MHead from "components/m-head"
import Player from "components/player"
import {mapGetters,mapMutations} from "vuex"
import {playMode} from "common/js/config"
export default {
	name: 'App',
	components:{
		MHead,
		Player
	},
	mounted(){this.addEventListener()},
	beforeDestroyed(){this.removeEventListener()},
	methods:{
		addEventListener(){
			let el=document.querySelector("audio");
			el.addEventListener("ended",this.nextPlay);
			el.addEventListener("error",this.error)
		},
		removeEventListener(){
			let el=document.querySelector("audio");
			el.removeEventListener("ended",this.nextPlay);
		},
		nextPlay(){
			let n=this.currentIndex;
			if(this.mode===playMode.sequence){
				n=(n+1)%this.sequenceList.length;
				this.setCurrentIndex(n)
				return;
			}else if(this.mode===playMode.random){
				let n2=math(this.sequenceList.length);
				while(n2==n){
					n2=math(this.sequenceList);
				}
				this.setCurrentIndex(n2);
				return;

			}
			document.querySelector("audio").currentTime=0;
			document.querySelector("audio").play();
		},
		...mapMutations({
			"setCurrentIndex":"SET_CURRENTINDEX"
		}),
		error(){
			let vkey1="33FA124D544F4AD4EAFA3BF7C01CF4473D9B622B57F9EAFFACBBD90BC3F39D9949C1B05BEF488FC6A4CB94AD47536A0E5625F092F0394C6C",
			vkey2="73FDEA16725066A25B4323A8FD6B070BB9B7C4D1E404D0690BF20DEB38E6ABB7BBA68BEC8FC1DF40877B9656F9F31037BAAC8E0DBC6C0BFA";
			if(this.vkey==vkey1){
			this.vkey=vkey2;
			return;}
			if(this.vkey==vkey2){
				this.vkey=vkey1;
			}

	}},
	computed:{

		audioSrc(){
			if(this.currentSong){
// let a="http://dl.stream.qqmusic.qq.com/C4000026DBoG28Y4Hz.m4a?guid=9322804048&vkey=73FDEA16725066A25B4323A8FD6B070BB9B7C4D1E404D0690BF20DEB38E6ABB7BBA68BEC8FC1DF40877B9656F9F31037BAAC8E0DBC6C0BFA&uin=0&fromtag=38";

				return `http://dl.stream.qqmusic.qq.com/C400${this.currentSong.songmid}.m4a?guid=9322804048&vkey=${this.vkey}&uin=0&fromtag=38`;
			}
			return"";
			
			
		},
		currentSong(){
			return this.sequenceList[this.currentIndex];
		},
		...mapGetters([
			"fullScreen",
			"sequenceList",
			"currentIndex",
			"mode"
			])
	},
	data(){
		return{
			vkey:"33FA124D544F4AD4EAFA3BF7C01CF4473D9B622B57F9EAFFACBBD90BC3F39D9949C1B05BEF488FC6A4CB94AD47536A0E5625F092F0394C6C"	
		}
	}
}

</script>
<style lang="less" type="text/less">
@import "~common/less/index";
@import "~common/less/mixin";

#app {
	// background-color: @color-background;
	
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	.list(column);
	.app-top{
		// height: 185px;
	}
}
	// // position: relative;
	// }
	#app-contain-wrap{
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
		overflow-y: scroll;
	}


	</style>



